.steps {
  min-height: 300px;
  height: 300px;
  overflow: hidden;
}

.step {
  position: absolute;
  top: 0;
  left: 0;
  min-height: 300px;
  height: 300px;
  width: 100%;
  opacity: 0;
  z-index: 1;
}

.step-inner-container {
  min-height: 300px;
  height: 300px;
}

.step.ng-hide-add,
.step.ng-hide-remove {
  -webkit-transition: all linear 0.5s;
  -o-transition: all linear 0.5s;
  -moz-transition: all linear 0.5s;
  transition: all linear 0.5s;
  display: block !important;
}

.step-transition {
  -webkit-transition: all linear 0.5s;
  -o-transition: all linear 0.5s;
  -moz-transition: all linear 0.5s;
  transition: all linear 0.5s;
}

.step.ng-hide-add.ng-hide-add-active,
.step.ng-hide-remove {
  opacity: 0;
}

.step-out {
  opacity: 0;
}

.step-in {
  opacity: 1;
}

.step.ng-hide-add,
.step.ng-hide-remove.ng-hide-remove-active {
  opacity: 1;
}

